.hero {
  background-color: var(--hero-bg-color);
  height: 502px;
  @include mq(tab){
    height: 416px;
  }
  @include mq(desk){
    height: 664px;
  }
}

.btn__hero{
  display: flex;
}

.btn__link {
  display: block;
  padding: 7px 13px;
  border-radius: 20px;
  font-family: var(--primary-font-family);
  font-weight: 400;
  font-size: 8px;
  line-height: 10px;
  align-items: center;
  text-align: center;

  @include mq(desk){
    padding: 11px 20px;
    border-radius: 20px;
    font-family: var(--primary-font-family);
    font-size: 14px;
    line-height: 18px;
  }

  &--white {
    background-color: var(--white-color);
    color: var(--accent-color);
  }

  &--pink {
    background-color: var(--accent-color);
    color: var(--white-color);
    margin-right: 8px;
    @include mq(desk){
      margin-right: 12px;
    }
  }
}



.hero__title {
  max-width: 218px;
  margin-bottom: 20px;
  color: var(--white-color);
  font-family: var(--title-font-family);
  font-weight: 400;
  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  @include mq(tab){
    max-width: 162px;
    font-size: 22px;
    line-height: 1.13;
  }

  @include mq(desk){
    max-width: 277px;
    margin-bottom: 24px;
    font-size: 38px;
    line-height: 1.3;
  }

  &--accent {
    color: var(--accent-color);
    font-size: 48px;
    line-height: 96.5%;
    @include mq(tab){
      font-size: 34px;
      line-height: 90.5%;
    }
    @include mq(desk){
      font-size: 58px;
      line-height: 90.5%;
    }
  }
}
.hero-content__position{
  @media screen and (max-width: 480px) {
    position: absolute;
    z-index: 2;
    top: 194px;
  }
}

.hero-content{
  position: relative;
  display: flex;
  justify-content: space-between;
  padding-top: 194px;
  padding-bottom: 158px;
  @include mq(tab){
    padding-top: 102px;
    padding-bottom: 190px;
  }
  @include mq(desk){
    padding-top: 147px;
    padding-bottom: 316px;
  }

}

.number{
  font-family: var(--title-font-family);
  color: var(--accent-color);
  font-weight: 400;
  font-size: 28px;
  line-height: 1.14;
  text-align: right;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  @include mq(mob-only){
    display: none;
  }
  @include mq(desk){
    font-size: 46px;
    line-height: 1.15;
  }

  &__margin{
    margin-bottom: 18px;
  }

  &__text{
    font-family: var(--primary-font-family);
    color: var(--white-color);
    font-weight: 400;
    font-size: 8px;
    line-height: 1.25;
    text-align: right;
    @include mq(desk){
      font-size: 14px;
      line-height: 1.2;
    }
  }
}

.number__position{
  @media screen and (max-width: 768px) {
    display: none;
  }
  @include mq(tab-only){
    position: absolute;
    z-index: 2;
    top: 102px;
    right: 0;
  }
}


