.container {
  //   width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;

  @include mq(mob) {
    width: 320px;
  }

  @include mq(fab) {
    width: 480px;
  }

  @include mq(tab) {
    width: 768px;
    padding-right: 32px;
    padding-left: 32px;
  }

  @include mq(desk) {
    position: relative;
    padding-right: 16px;
    padding-left: 16px;
    width: 1200px;
  }
}
